"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import useGameStore from "@/store/gameStore";
import { Player } from "@/types";
import DartBoard from "@/components/DartBoard";

export default function GameSetupPage() {
  const router = useRouter();
  const { players, createGame } = useGameStore();
  const [selectedPlayers, setSelectedPlayers] = useState<string[]>([]);
  const [startingScore, setStartingScore] = useState<number>(501);

  const handlePlayerToggle = (playerId: string) => {
    setSelectedPlayers(prev => {
      if (prev.includes(playerId)) {
        return prev.filter(id => id !== playerId);
      } else {
        if (prev.length < 10) {
          return [...prev, playerId];
        }
        return prev;
      }
    });
  };

  const handleStartGame = () => {
    if (selectedPlayers.length === 0) {
      alert("Välj minst en spelare");
      return;
    }

    const game = createGame(selectedPlayers, startingScore);
    router.push(`/game/${game.id}`);
  };

  const commonScores = [301, 501, 701];

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8 text-center text-white">Nytt Dartspel</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Player Selection */}
        <div className="player-card overflow-hidden md:col-span-1">
          <h2 className="player-header">
            Välj spelare ({selectedPlayers.length}/10)
          </h2>
          
          {players.length === 0 ? (
            <div className="p-6 text-center">
              <p className="text-gray-400 mb-4">Inga spelare har lagts till än.</p>
              <button
                onClick={() => router.push("/players")}
                className="px-5 py-2 bg-red-600 text-white rounded-md hover:bg-red-500 transition-colors"
              >
                Lägg till spelare
              </button>
            </div>
          ) : (
            <div className="p-4">
              <div className="max-h-96 overflow-y-auto">
                {players.map((player: Player) => (
                  <div key={player.id} className="py-3 px-2 border-b border-gray-800">
                    <label className="flex items-center space-x-3 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={selectedPlayers.includes(player.id)}
                        onChange={() => handlePlayerToggle(player.id)}
                        className="w-5 h-5 text-red-600 rounded focus:ring-red-500 bg-gray-700 border-gray-600"
                      />
                      <span className="font-medium text-white">{player.name}</span>
                    </label>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
        
        {/* Visual Dartboard */}
        <div className="md:col-span-1 flex justify-center items-center">
          <DartBoard size={200} />
        </div>
        
        {/* Game Settings */}
        <div className="player-card overflow-hidden md:col-span-1">
          <h2 className="player-header">Spelinställningar</h2>
          
          <div className="p-6 space-y-6">
            <div>
              <label className="block font-medium mb-3 text-white" htmlFor="starting-score">
                Startpoäng
              </label>
              <div className="flex gap-2 mb-4">
                {commonScores.map(score => (
                  <button
                    key={score}
                    type="button"
                    onClick={() => setStartingScore(score)}
                    className={`px-4 py-2 rounded-md font-bold ${
                      startingScore === score
                        ? "bg-red-600 text-white"
                        : "bg-gray-700 text-white hover:bg-gray-600"
                    } transition-colors`}
                  >
                    {score}
                  </button>
                ))}
              </div>
              <input
                id="starting-score"
                type="number"
                value={startingScore}
                onChange={(e) => setStartingScore(parseInt(e.target.value) || 0)}
                min="1"
                className="w-full px-4 py-3 bg-gray-800 border border-gray-700 text-white rounded-md focus:outline-none focus:ring-2 focus:ring-red-500"
              />
            </div>
            
            <div className="pt-4">
              <button
                onClick={handleStartGame}
                disabled={selectedPlayers.length === 0}
                className={`w-full py-4 rounded-md font-bold text-lg ${
                  selectedPlayers.length === 0
                    ? "bg-gray-700 text-gray-500 cursor-not-allowed"
                    : "bg-red-600 text-white hover:bg-red-500"
                } transition-colors`}
              >
                Starta spel
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 